.cardContainer {
	cursor: pointer;
	height: 400px;
	width: 400px;
	perspectice: 800px;
	position: relative;
	margin: auto;
	-webkit-transition: ease-in;
	-o-transition: ease-in;
	transition: ease-in;
}

.Card {
	height: 400px;
	width: 400px;
	position: absolute;
	border: 2px solid #5585ff;
	border-radius: 20%;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-transition: all 0.4s ease-in-out;
	-o-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
}

.Card:hover {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
	-webkit-transition: all 0.4s ease-in-out;
	-o-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
}

.Card .front {
	height: 400px;
	width: 400px;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	position: absolute;
	overflow: hidden;
	text-align: center;
}

.Card .back {
	height: 400px;
	width: 400px;
	text-align: center;
		-webkit-backface-visibility: hidden;
		        backface-visibility: hidden;
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

.def {
	text-align: center;
	padding-top: 45%;
	font-weight: bold;
}

.word {
	text-align: center;
	padding-top: 45%;
		font-weight: bold;

}



